.Login {
    display: flex;
    height: calc(100vh - 54px) ;
}

.Photo {
  width: 65%;
}

.LoginForm {
  width: 35%;
}

@media(max-width: 1250px){
    .Photo {
        width: 40%;
    }

    .LoginForm {
        width: 60%;
    }
}

@media(max-width: 820px){
    .Photo {
        width: 10%;
    }

    .LoginForm {
        width: 90%;
    }
}

@media(max-width: 720px) {
    .Login {
        height: 100vh;
    }

    .LoginForm {
        width: 100%;
    }

    .Photo {
        display: none;
    }
}

.Form {
    padding: 30px 0px;
    margin: 50px auto;
    max-width: 90%;

}

.Form h1 {
    font-weight: lighter;
    text-transform: uppercase;
    font-size: 30px;
    padding-bottom: 10px;
}

.Form h2 {
    font-weight: normal;
    margin-top: 10px;
}

.Form h1 svg {
    margin-right: 5px;
    font-size: 30px;
}

.Input input {
   outline: none;
   border: 1px solid lightgray;
   padding: 10px 10px;
   width: 100%;
}

.Input {
    padding-bottom: 10px;
}

.Input label {
    display: block;
    text-transform: uppercase;
    color: gray;
    margin: 15px 0px 10px 0px;
}

.Input label span {
    color: red;
    padding: 2px;
    font-size: 17px;
}

.Input svg {
    color: red!important;
}

.RememberMe {
    padding: 10px 0;
}

.Error {
    color: #fff;
    background: red;
    padding: 8px 14px;
    margin: 10px 0;
}

.Form button{
    padding: 12px 25px;
    margin-top: 20px;
    font-size: 15px;
    outline: none;
    border: none;
    color: #fff;
    background: #2B5278;
}

.Captcha {
    padding: 10px 0px;
}

.Captcha img {
    height: 70px;
    border: 1px solid lightgray;
}

.Captcha input {
    outline: none;
    border: 1px solid lightgray;
    padding: 10px 10px;

}

